import React, { useState,useEffect, useRef } from 'react'
import { useParams,useNavigate } from 'react-router-dom'

import ItemCard from './ItemCard'
import { apiurl } from './apiurl'
import Footer from './Footer'


// /category
export default function CategoryStore(props) {
  const cat = useParams().category
  // alert(cat)
  let curr_cat_obj = {}; 

  const productPerPage = 12

  const handlepageclick = (newCount) =>{
    if (newCount > 0 && newCount <=Math.ceil(categoryProducts.totalProductCount / productPerPage) ){
      setPageCount(newCount)
    }
  }
  
  
  const [products,setProducts] = useState(null)
  const [categoryProducts,setCategoryProducts] = useState(null)
  const [pageCount,setPageCount] = useState(1)

  const thefetch = (payload)=>{
    fetch(`${apiurl}/categories/getcategoryproduct`,{
      method:"POST",
      body:JSON.stringify(payload),
      headers : {"Content-Type":"application/json"}

    })
    .then(res=>res.json())
    .then(data=>{
      setCategoryProducts(data)
      console.log("search data is ,",data)
    })
    .catch(err=>console.log(err))
  }

  useEffect(() => {
    // filter the object to find curr_cat_obj._id of the category
    try{
      curr_cat_obj = props.categories.filter(elem=>elem.category_name === cat)[0]
      // alert(`cur cat obj ${curr_cat_obj._id}`)
      const payload = {"category_ref":curr_cat_obj._id,productPerPage:productPerPage,pageNumber: pageCount}
      thefetch(payload)
      
    }catch(err){
      console.log("directly refreshed to landed here")
      fetch(`${apiurl}/categories`).then(res=>res.json())
      .then(data=>{
        console.log("data fetched",data)
        // alert("got data")
        // setCCO(data.data.filter(elem=>elem.category_name===cat)[0])
        curr_cat_obj = data.data.filter(elem=>elem.category_name===cat)[0]
        const payload = {"category_ref":curr_cat_obj._id}
        console.table(payload)
        thefetch(payload)
      })
    }
  }, [cat,pageCount])




  return (
    <>
    <div className='container'>
      <div className='row row-cols-2 row-cols-md-4'>
        <div className='col'>

        </div>
      </div>
        <h1>{cat}</h1>
        {/* <div className='row'> */}
        <div className='row row-cols-lg-6 row-cols-md-4 row-cols-2'>
        {categoryProducts && categoryProducts.data.map(elem=>(
            <div className='col mb-3'>
          <ItemCard img={elem} />
          </div>
            // <ItemCard img={elem} />
        ))}
        </div>
    </div>

          {/* pagination */}
          <div className='text-center m-3'>
              <div className='btn btn-outline-secondary' onClick={()=>handlepageclick(pageCount-1)}>-</div>&nbsp;
              showing page {pageCount} of {categoryProducts && Math.ceil(categoryProducts.totalProductCount / productPerPage)} 
              &nbsp;<div className="btn btn-outline-secondary" onClick={()=>handlepageclick(pageCount+1)}>+</div>

            </div>

    <Footer />
    </>
  )
}
